  import urls from '../../utils/urls.js'
	export default {
		data() {
			return {
				current: 0,
        sliderAnim: {},
        taskList: [],
				id:'',
				isLogin: this.$store.state.isLogin
			}
		},
    onShow() {
      // let id = uni.getStorageSync('user').id;
			console.log(this.$store.state.isLogin);
			if (this.$store.state.isLogin==false) {
			  uni.showModal({
			    title: '提示',
			    content: '您还没有登录，是否登录？',
			    success: (res) => {
			      if (res.confirm) {
			        uni.navigateTo({
			          url: '../common/login'
			        })
			      }
			    }
			  })
			}else{
				let id = JSON.parse(uni.getStorageSync("userInfo")).id  // 字符串转对象  取id
				uni.request({
				  url: urls.HTTP + '/usertask/newbietask',
				  method: 'POST',
				  data: {
				    uid: id
				  },
				  success: (res) => {
				    //console.log('新手任务', JSON.stringify(res.data.task))
				    this.taskList = res.data.task
				  }
				})
			}
			// 创建一个动画实例
			var animation = uni.createAnimation({
				duration: 300,
			  timingFunction: 'ease'
			})
			this.animation = animation
			
			this.changeClass(0);
    },
    computed: {
      isOne () {
        if (this.current === 0) {
          return true
        } else if (this.current === 1) {
          return false
        }
      }
    },
    methods: {
      changeClass: function (n) {
        this.current = n        
		let id = '';
		if(this.isLogin){
			id = JSON.parse(uni.getStorageSync('userInfo')).id
		}else{
			 id = 0
		}
        let url = '/usertask/newbietask'
        if (n === 0) {
          this.animation.left(0).step()
          this.sliderAnim = this.animation.export()
          url = '/usertask/newbietask'
        } else {
          this.animation.left(100 + '%').step()
          this.sliderAnim = this.animation.export()
          url = '/usertask/dailytask'
        }
        uni.request({
          url: urls.HTTP + url,
          method: 'POST',
          data: {
            uid: id
          },
          success: (res) => {
            this.taskList = res.data.task
          }
        })
      },
      receive: function (item) {        
		let id = '';
		if(this.isLogin){
			id = JSON.parse(uni.getStorageSync('userInfo')).id
		}else{
			 id = 0
		}
        let tid = item.id
        let type = item.type
        uni.request({
          url: urls.HTTP + '/usertask/receivetask',
          method: 'POST',
          data: {
            uid: id,
            tid: tid,
            type: type
          },
          success: (res) => {
            // console.log('领取', res)
            if (res.data.code === 0) {
              this.taskList = res.data.task
              uni.showToast({
                title: '领取成功！'
              })
            } else {
              uni.showToast({
                title: res.data.user,
                icon: 'none'
              })
            }
            
          }
        })
      },
      doIt: function (item) {
        let url = item.link
        uni.navigateTo({
          url: url
        })
        uni.switchTab({
          url: url
        })
      }
    }
	}
